import React from "react";

// import component
import CategoryItem from "./CategoryItem";

// import assets
import visitingCardsSvg from "../../assets/category-icons/VISITINGCARD.png";
import flightSvg from "../../assets/category-icons/aeroplane.png";
import shoppingBagSvg from "../../assets/category-icons/SHOPPING.png";
import groupSvg from "../../assets/category-icons/CONFRENCE.png";
import storeSvg from "../../assets/category-icons/VEHICLES.png";
import vectorSvg from "../../assets/category-icons/MEDICAL.png";
import fileCopySvg from "../../assets/category-icons/file_copy_24px.svg";
import { isLoggedIn } from "../login/TokenProvider";
import DineOut from "../../assets/category-icons/DINEOUT.png";
import Insurance from "../../assets/category-icons/insurance.png";
import Education from "../../assets/category-icons/education.png";
import Publicity  from "../../assets/category-icons/PUBLICITY.png";
import KYC  from "../../assets/category-icons/KYC.png"

import Utility  from "../../assets/category-icons/UTILITY.png";
import SocialInvites  from "../../assets/category-icons/SOCIALINVITES.png"

interface Categories {
  title: string;
  image: string;
}

// categories available
const categories: Categories[] = [
  {
    title: "Visiting Cards",
    image: visitingCardsSvg,
  },
  {
    title: "Travel Bookings",
    image: flightSvg,
  },
  {
    title: "dineOut",
    image: DineOut,
  },
  {
    title: "Shopping",
    image: shoppingBagSvg,
  },
  {
    title: "Medical",
    image: vectorSvg,
  },
  {
    title: "Vehicles",
    image: storeSvg,
  },
  {
    title: "Conference",
    image: groupSvg,
  },
  {
    title: "Publicity Material",
    image: Publicity,
  },
  {
    title: "Social Invites",
    image: SocialInvites,
  },
  {
    title: "Insurance Policy",
    image: Insurance,
  },
  {
    title: "Utility",
    image: Utility,
  },
  {
    title: "Education",
    image: Education,
  },
  {
    title: "KYC",
    image: KYC,
  },
  {
    title: "Others",
    image: fileCopySvg,
  },
];

/*
  changeCategory prop passed from the parent is sent to the child
  so it can update the current selected category to update the page
*/
type props = {}
type states = {
  notificationsCount: number
};

class CategoryHome extends React.Component<props, states> {
  constructor(props: props) {
    super(props)

    if (!isLoggedIn()) {
      window.location.replace('/')
    }

    this.state = {
      notificationsCount: 0
    }
  }

  render() {
    return (
      <React.Fragment>
        <div className="category-slots">
          {categories.map((category, index) => {
            return (
              <CategoryItem
                key={index}
                title={category.title}
                image={category.image}
              />
            );
          })}
        </div>
      </React.Fragment>
    )
  }
}

export default CategoryHome;
